import React from 'react'
import { Button, Form, Input, message } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { Link, useNavigate } from 'react-router-dom'
import "./less/login.less"
import logoImg from '../assets/images/logo.png'
import { RegisterApi } from '../request/api';

export default function Register() {
    const navigate = useNavigate()

    const onFinish = (values) => {
        console.log('Success:', values);
        RegisterApi({
            username: values.username, 
            password: values.password
        }).then(res=>{
            console.log(res);
            if (res.errCode === 0) {
                message.success(res.message);
                setTimeout(()=>navigate('/login'), 1500)
            } else {
                message.error(res.message);
            }
        })
    };
    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <div className='login'>
            <div className='login_box'>
                <img src={logoImg} alt="" />
                <Form name="basic" initialValues={{ remember: true }} onFinish={onFinish} onFinishFailed={onFinishFailed} autoComplete="off">
                    <Form.Item name="username" rules={[{ required: true, message: '!' }]}>
                        <Input size='large' prefix={<UserOutlined />} placeholder="" />
                    </Form.Item>

                    <Form.Item name="password" rules={[{ required: true, message: '!' }]}>
                        <Input.Password size='large' prefix={<LockOutlined />} placeholder="" />
                    </Form.Item>

                    <Form.Item name="confirm" dependencies={['password']} hasFeedback
                        rules={[
                            { required: true, message: '!' },
                            ({ getFieldValue }) => ({
                                validator(_, value) {
                                    if (!value || getFieldValue('password') === value) {
                                        return Promise.resolve();
                                    }
                                    return Promise.reject(new Error('!'));
                                },
                            }),
                        ]}
                    >
                        <Input.Password size='large' prefix={<LockOutlined />} placeholder="" />
                    </Form.Item>

                    <Form.Item>
                        <Link to="/login"></Link>
                    </Form.Item>

                    <Form.Item>
                        <Button size='large' type="primary" htmlType="submit" block></Button>
                    </Form.Item>
                </Form>
            </div>
        </div>
    )
}